.benefits {
    min-height: 473px;
    padding: 70px 0 50px 0;
    background-color: #F6F6F6;
    color: #0D0D0D;

    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        font-weight: 700;
        font-size: 30px;
        line-height: 30px;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 0;
    }

    &__items {
        margin-top: 50px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
    }

    &__item {
        width: 270px;
        text-align: center;
        padding: 15px;
    }

    &__img {
        display: block;
        margin: 0 auto;
        margin-bottom: 30px;
        width: 70px;

        &_animated {
            animation-name: heartbeat;
            animation-duration: 2.5s;
            animation-timing-function: ease;
            animation-iteration-count: infinite;
        }
    }

    &__subtittle {
        font-weight: 700;
        font-size: 18px;
        line-height: 29px;
    }

    &__desc {
        margin-top: 20px;
        font-weight: 300;
        font-size: 14px;
        line-height: 18px;

        a {
            color: $main-color;
        }
    }
}